:root {
  --bg-white: #fff;
  --bg-overlay: #0000004d;
  --text-color: #000;
  --close-icon-color: #000;
}

.custom-modal-overlay {
  background-color: var(--bg-overlay);
  z-index: 999;
  justify-content: center;
  align-items: center;
  position: fixed;
  inset: 0;
}

.custom-modal {
  z-index: 1;
  background-color: var(--bg-white);
  width: 100%;
  height: 100%;
  padding: 10px;
  display: none;
  position: relative;
  overflow: auto;
}

.custom-modal-content {
  background-color: #fefefe;
  width: 80%;
  margin: 5% auto;
  padding: 20px;
}

.custom-modal-close {
  color: var(--close-icon-color);
  float: right;
  font-size: 28px;
  font-weight: bold;
  position: absolute;
  top: 5px;
  right: 5px;
}

.custom-modal-close:hover, .custom-modal-close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

